<template>
  <main>
    <section class="about" id="about">
      <div class="about__content">
        <img
          class="animation"
          src="@/assets/Image/about-me.jpg"
          alt=""
          data-speed="800"
        />
        <div class="about__content-sub animation" data-speed="1000">
          <h2 class="about__content-title">
            Muxtorov Muxammad Raximberdi o’g’li
          </h2>
          <p class="about__content-text">
            Uzbekistan Toshkent shahar uchtepa tumanida 1995 yil 26 oktabrda
            tugulganman 2020 buyon manual terapiaya va yumeyxo terapiaya bilan
            shugulanib kelaman men Xitoyda oqiganman va xindiston va dubai
            davlatlarida malaka owirganman va Uzbekistan hamma vilayatlarida
            bolganman va ishlaganman yutuqlarim Uzbekistan xalq tabobatida xalq
            tabibiman va juda kop davlatlarda malaka oshirgan diplomlarim mavjud
          </p>
        </div>
      </div>
      <div class="about__slides">
        <swiper
          :effect="'coverflow'"
          :grabCursor="true"
          :centeredSlides="true"
          :slidesPerView="'auto'"
          :coverflowEffect="{
            rotate: 50,
            stretch: 0,
            depth: 100,
            modifier: 1,
            slideShadows: true,
          }"
          :pagination="true"
          :modules="modules"
          class="mySwiper"
        >
          <swiper-slide>
            <img src="@/assets/Image/sertification/tech.png" />
          </swiper-slide>
          <swiper-slide>
            <img src="@/assets/Image/sertification/tech.png" />
          </swiper-slide>
          <swiper-slide>
            <img src="@/assets/Image/sertification/tech.png" />
          </swiper-slide>
          <swiper-slide>
            <img src="@/assets/Image/sertification/tech.png" />
          </swiper-slide>
          <swiper-slide>
            <img src="@/assets/Image/sertification/tech.png" />
          </swiper-slide>
        </swiper>
      </div>
    </section>
  </main>
</template>

<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
import { Swiper, SwiperSlide } from "swiper/vue";
import { EffectCoverflow, Pagination } from "swiper";
import "swiper/css";
import "swiper/css/effect-coverflow";
import "swiper/css/pagination";

export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      modules: [EffectCoverflow, Pagination],
    };
  },
  
  mounted() {
    const parent = document.querySelector(`#about`);
    let observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry, index) => {
          if (entry.isIntersecting) {
            const el = parent.querySelectorAll(".animation");
            el.forEach((i) => {
              let sp = i.getAttribute("data-speed");
              i.classList.add("animate__animated");
              i.style.transition = sp + "ms";
            });
          }
        });
      },
      { threshold: 0 }
    );

    observer.observe(parent);
  },
};
</script>

<style>
.swiper {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 400px;
  height: 600px;
}

.swiper-slide img {
  display: block;
  width: 100%;
}

.swiper-pagination-bullet-active {
  background: #263640;
}

</style>